<template>
  <api-request
    :resource="load()"
    v-model="pendingPayments"
    effect="fadeIn"
    spinner="PulseLoader"
    spinner-color="white"
    spinner-padding="150px"
    :spinner-scale="2">
    <NoResults :visible="((pendingPayments.data || {}).payments || []).length === 0"/>
    <div v-if="((pendingPayments.data || {}).payments || []).length > 0">
      <div class="table">
        <div class="table__header">
          <div class="table__header__address">Address</div>
          <div class="table__header__amount">Amount</div>
          <div class="table__header__payment">Payment Details</div>
          <div class="table__header__status">Status</div>
        </div>
        <div class="table__body">
          <div v-for="payment in (pendingPayments.data || {}).payments" class="table__body-item">
            <div class="table__body-item__address">
              <img :src="getCoinLogo(pendingPayments.data.addresses[payment['target_address']].payout_ticker)" :alt="pendingPayments.data.addresses[payment['target_address']].payout_ticker">
              <span>{{ pendingPayments.data.addresses[payment['target_address']].payout_address }}</span>
            </div>
            <div class="table__body-item__amount">
              <span>{{ payment.amount }} {{ payment.internal_ticker }}</span>
            </div>
            <div class="table__body-item__payment" v-if="payment.txid">
              <div>{{ payment.txid }}</div>
            </div>
            <div class="table__body-item__status" v-if="payment.txid">
              <font-awesome-icon icon="check" v-if="payment.confirmations >= 3" />
              <span v-if="payment.confirmations < 3">{{ payment.confirmations }}/3</span>
            </div>
            <div class="table__body-item__status" v-if="!payment.txid">
              <font-awesome-icon icon="clock" />
            </div>
          </div>
        </div>
      </div>
    </div>
  </api-request>
</template>

<script>
import NoResults from '@/components/NoResults.vue'

export default {
  name: 'PaymentsTab',
  props: {

  },
  data() {
    return {
      pendingPayments: {},
    }
  },
  components: {
    NoResults
  },
  methods: {
    getCoinLogo: (ticker) => {
      return require('../assets/coins/' + ticker + '.png')
    },
    load () {
        return (params) => this.$api.getMyPayments(params)
    }
  }
}
</script>

<style scoped>
  /* For mobile: */
  .table {
    font-size: 12px;
  }

  .table__header {
    font-size: 15px;
    background-color: #393731;
    color: #ccc;
  }

  .table__header,
  .table__body-item {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;
    padding: 15px;
  }

  .table__body-item {
    background-color: #fff;
  }

  .table__body-item:nth-child(even) {
    background-color: #eee;
  }

  .table__header__address,
  .table__body-item__address {
    width: 30%;
    flex-shrink: 0;
  }

  .table__body-item__address {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;
  }

  .table__body-item__address img {
    height: 13px;
    margin-right: 3px;
  }

  .table__body-item__address span {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding-right: 3px;
  }

  .table__header__amount,
  .table__body-item__amount {
    width: 25%;
    flex-shrink: 0;
    padding-left: 5px;
    display: flex;
  }

  .table__body-item__amount span {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding-right: 3px;
  }

  .table__header__payment,
  .table__body-item__payment {
    width: 30%;
    flex-shrink: 0;
    padding-left: 5px;
  }

  .table__body-item__payment div {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding-right: 3px;
  }

  .table__header__status,
  .table__body-item__status {
    width: 15%;
    flex-shrink: 0;
    text-align: right;
  }

  /* For tablet: */
  @media only screen and (min-width: 768px) {
    .table__header,
    .table__body-item {
      padding: 15px 20px;
    }

    .table__header__payment,
    .table__body-item__payment {
      width: 35%;
    }

    .table__header__status,
    .table__body-item__status {
      width: 10%;
    }
  }

  /* For other tablet size: */
  @media only screen and (min-width: 1024px) {
    .table__header__amount,
    .table__body-item__amount {
      width: 20%;
    }

    .table__header__payment,
    .table__body-item__payment {
      width: 42%;
    }

    .table__header__status,
    .table__body-item__status {
      width: 8%;
    }
  }
</style>
