<template>
  <modal name="tickets-modal" @before-open="beforeOpen" :height="getModalHeight()" :width="getModalWidth()">
    <api-request :resource="load()" v-model="tickets" spinner="">
      <div class="tickets-modal" style="height: 100%;">
        <div class="tickets-modal__content">
          <div class="tickets-modal__payout-address">
            <span class="tickets-modal__payout-address__label">Address: </span>
            <span class="tickets-modal__payout-address__value">{{ payoutAddress }}</span>
          </div>

          <div class="draw-table">
            <div class="draw-table__header">
              <div class="draw-prize__date">Date</div>
              <div class="draw-table__tickets">Ticket No</div>
              <div class="draw-table__tickets">Accuracy</div>
            </div>
            <div class="draw-table__results">
              <div v-for="ticket in (tickets.data || {}).tickets_list" class="draw-table__prize">
                <div class="draw-prize__date">
                  {{ new Date(ticket.created_timestamp * 1000).toLocaleDateString("en-US") }}
                  {{ new Date(ticket.created_timestamp * 1000).toLocaleTimeString("en-US") }}
                </div>
                <div class="draw-table__tickets">
                  {{ ticket.winner_hash_decimal }}
                </div>
                <div class="draw-table__tickets">
                  <span v-if="ticket.isClosed">{{ ticket.accuracy.toFixed(3) }}%</span>
                  <span v-if="!ticket.isClosed">?</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </api-request>
  </modal>
</template>

<script>
export default {
  name: 'TicketsModal',
  data() {
    return {
      tickets: [],
      addressId: 0,
      drawId: 0,
      payoutAddress: ''
    }
  },
  methods: {
    getModalWidth() {
      if (window.innerWidth < 768) {
        return '80%';
      } else if (window.innerWidth < 1200) {
        return '70%';
      }
      return '50%';
    },
    getModalHeight() {
      if (window.innerWidth < 768) {
        return '80%';
      } else if (window.innerWidth < 1200) {
        return '60%';
      }
      return '50%';
    },
    hide () {
      this.$modal.hide('tickets-modal');
    },
    beforeOpen (event) {
      this.addressId = event.params.addressId;
      this.drawId = event.params.drawId;
      this.payoutAddress = event.params.payoutAddress;
    },
    load() {
      return () => this.$api.getTickets(this.addressId, this.drawId)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .tickets-modal__content {
    height: 100%;
    padding: 20px 10px;
  }

  .tickets-modal__payout-address {
    text-align: center;
    margin-bottom: 20px;
  }

  .tickets-modal__payout-address__label {
    font-size: 12px;
    font-weight: 700;
  }

  .tickets-modal__payout-address__value {
    font-size: 12px;
    word-wrap:break-word;
  }

  .draw-table {
    height: calc(100% - 37px);
    font-size: 12px;
  }

  .draw-table__header {
    font-weight: 700;
  }

  .draw-table__results {
    height: inherit;
    overflow-y: scroll;
  }

  .draw-table__header,
  .draw-table__prize {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;
    padding: 5px;
  }

  .draw-table__prize:nth-child(odd) {
    background-color: #eee;
  }

  .draw-table__prize.green-background {
    background-color: #7dcea0;
    font-weight: 700;
  }

  .draw-prize__date {
    width: 40%;
    text-align: center;
  }

  .draw-prize__tickets,
  .draw-table__tickets {
    width: 30%;
    float: right;
    text-align: center;
  }
</style>

<style>
  .v--modal-overlay {
    background: rgba(0, 0, 0, 0.5) !important;
  }

  .api-request {
    height: 100%;
  }
</style>
