<template>
  <BasePage>
    <div class="app__page-title">
      Results & Winnings
    </div>
    <api-request
      :resource="load()"
      v-model="drawHistory"
      effect="fadeIn"
      spinner="PulseLoader"
      spinner-color="white"
      spinner-padding="150px"
      :spinner-scale="2">
        <Accordion v-for="(item, index) in (drawHistory.data || {}).draws || []" :items="[item]" :selectors="{ id: 'id', title: 'title', content: 'content' }" :active="index === 0 || index === 1 ? [0] : []" :class="{ 'draw-active': item.accepting_payments === 't' }" :collapseAll="false" ref="accordion">
          <template slot="title" slot-scope="{ item }">
            <div class="draw-header">
              <div class="draw-info">
                <div class="draw-info__item" title="Draw Number">
                  <font-awesome-icon icon="hashtag" />
                  <span>{{ item.draw_id }}</span>
                </div>
                <div class="draw-info__item" title="Draw Date">
                  <font-awesome-icon icon="calendar-alt" />
                  <span>{{ new Date(item.utime * 1000).toLocaleDateString("en-US") }}</span>
                </div>
                <div class="draw-info__item" title="Total Tickets Bought">
                  <font-awesome-icon icon="ticket-alt" />
                  <span>{{ item.tickets }}</span>
                </div>
                <div class="draw-info__item" title="Prize Pool">
                  <font-awesome-icon icon="gem" />
                  <span>{{ new Number(item.prize_pool).toFixed(4) }} {{ item.std_coin_ticker }}</span>
                </div>
                <div class="draw-info__item show-for-large" title="Metronome Winning Hash">
                  <font-awesome-icon icon="cubes" />
                  <span v-if="item.current_block_height < item.metronome_winner_height">Waiting for Block #{{ item.metronome_winner_height }}</span>
                  <span v-if="item.current_block_height >= item.metronome_winner_height">
                    0x{{ item.metronome_winner_hash }}
                    <span style="color: #ff8000; font-size: 11px;" v-if="item.current_block_height - item.metronome_winner_height < 3">
                      confirming
                    </span>
                  </span>
                </div>
              </div>
              <div class="draw-header-angle-up">
                <font-awesome-icon icon="angle-up" />
              </div>
              <div class="draw-header-angle-down">
                <font-awesome-icon icon="angle-down" />
              </div>
            </div>
          </template>
          <template slot="content" slot-scope="{ item }">
              <div class="draw-winning-hash hide-for-large" v-if="item.current_block_height < item.metronome_winner_height">
                <span class="draw-winning-hash__label">Winning Hash: </span>
                <span class="draw-winning-hash__value">Waiting for Block #{{ item.metronome_winner_height }}</span>
              </div>

              <div class="draw-winning-hash hide-for-large" v-if="item.current_block_height >= item.metronome_winner_height">
                <span class="draw-winning-hash__label">Winning Hash: </span>
                <span class="draw-winning-hash__value">0x{{ item.metronome_winner_hash }}</span>
                <span style="color: #ff8000; font-size: 11px;" v-if="item.current_block_height - item.metronome_winner_height < 3">
                  confirming
                </span>
              </div>

              <div class="draw-table">
                <div class="draw-table__header">
                  <div class="draw-table__address">Address</div>
                  <div class="draw-table__prize-amount">Prize</div>
                  <div class="draw-table__status">Status</div>
                </div>
                <div class="draw-table__results">
                  <div class="draw-table__prize" v-for="prize in (drawHistory.data || { prizes: {payments: {}}}).prizes.payments[item.draw_id] || getDefaultPrizes(item.prize_pool, item.std_coin_ticker)">
                    <div class="draw-prize__address">
                      <img v-if="prize.payout_address_id" :src="getCoinLogo(prize.prize_ticker)" :alt="prize.prize_ticker">
                      <span v-if="!prize.payout_address_id">
                          <i>To be defined</i>
                      </span>
                      <span v-if="prize.payout_address_id">
                          {{ drawHistory.data.prizes.addressInfo[prize.payout_address_id]["payout_address"] }}
                      </span>
                    </div>
                    <div class="draw-prize__prize-amount">
                      {{ new Number(prize.prize_amount).toFixed(8) }} {{ prize.prize_ticker }}
                    </div>
                    <div class="draw-prize__status" v-if="prize.payout_address_id && prize.payment_status === 'finished'">
                      <font-awesome-icon icon="check" />
                    </div>
                    <div class="draw-prize__status" v-else-if="prize.payout_address_id && prize.payment_status === 'pending'">
                      <font-awesome-icon icon="clock" />
                      &nbsp; {{ prize.confirmations }}/3
                    </div>
                    <div class="draw-prize__status" v-else-if="prize.payout_address_id && prize.payment_status === 'failed'">
                      <font-awesome-icon icon="times" />
                    </div>
                    <div class="draw-prize__status" v-else-if="!prize.payout_address_id">
                      -
                    </div>
                  </div>
                </div>
              </div>

              <div class="draw-details">
                <router-link class="draw-details__link" :to="'/draw-details/' + item.draw_id">
                  <span>See Draw Details</span>
                </router-link>
              </div>
          </template>
        </Accordion>
    </api-request>

    <div style="height: 850px" />
  </BasePage>
</template>

<script>
import BasePage from '@/pages/BasePage.vue'
import Accordion from '@profesia/vue-accordion-component'

export default {
  name: 'History',
  data: function () {
    return {
      drawHistory: {
        data: {
          addressInfo: {}
        }
      }
    }
  },
  components: {
    BasePage,
    Accordion
  },
  methods: {
    getCoinLogo: (ticker) => {
      return require('../assets/coins/' + ticker + '.png')
    },
    load () {
        return (params) => this.$api.getDrawHistory(params)
    },
    getDefaultPrizes (prizePool, ticker) {
        const decay = [0.45,0.25,0.14,0.05,0.04,0.03,0.02,0.01,0.0075,0.0025];
        let defaultPrizes = [];
        for (let i = 0; i < decay.length; ++i) {
            defaultPrizes.push({
                'prize_amount': (decay[i] * prizePool).toFixed(5),
                'prize_ticker': ticker,
                'payment_status': 'pending'
            });
        }
        return defaultPrizes;
    }
  }
}
</script>

<style scoped>
  /* For mobile: */
  .app__page-title {
    padding: 40px 0 20px;
    font-size: 18px;
    font-weight: 700;
  }

  .draw-header {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
  }

  .draw-info {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
  }

  .draw-info__item {
    margin-right: 20px;
    margin-bottom: 5px;
  }

  .draw-info__item svg {
    margin-right: 5px;
  }

  .draw-header-angle-up {
    display: none;
  }

  .draw-winning-hash {
    margin-bottom: 20px;
    text-align: center;
  }

  .draw-winning-hash__label {
    font-size: 12px;
    font-weight: 700;
  }

  .draw-winning-hash__value {
    font-size: 12px;
    word-wrap:break-word;
  }

  .draw-table {
    font-size: 12px;
  }

  .draw-table__header {
    font-weight: 700;
  }

  .draw-table__header,
  .draw-table__prize {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;
    padding: 5px;
  }

  .draw-table__prize:nth-child(odd) {
    background-color: #eee;
  }

  .draw-table__address,
  .draw-prize__address {
    width: 60%;
  }

  .draw-prize__address {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;
  }

  .draw-prize__address img {
    height: 13px;
    margin-right: 3px;
  }

  .draw-prize__address span {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding-right: 3px;
  }

  .draw-table__prize-amount,
  .draw-prize__prize-amount {
    width: 20%;
    padding-left: 10px;
    text-align: right;
  }

  .draw-table__status,
  .draw-prize__status {
    width: 20%;
    padding-left: 10px;
    text-align: right;
  }

  .draw-details {
    margin-top: 20px;
    text-align: center;
  }

  .draw-details__link {
    font-size: 12px;
    font-weight: 700;
    transition: color 0.3s ease;
  }

  .draw-details__link:hover {
    color: #ff8000;
  }

  /* For tablet: */
  @media only screen and (min-width: 768px) {
    .draw-table__address,
    .draw-prize__address {
      width: 70%;
    }

    .draw-table__status,
    .draw-prize__status {
      width: 10%;
    }
  }
</style>

<style>
  .vue-accordion-item {
    border: 0 !important;
    margin-bottom: 5px;
  }

  .vue-accordion-item-active {
    border: 0 !important;
  }

  .vue-accordion-item-active .draw-header-angle-up {
    display: block;
  }

  .vue-accordion-item-active .draw-header-angle-down {
    display: none;
  }

  .vue-accordion-title {
    background-color: #393731 !important;
    padding: 10px 15px !important;
    font-size: 14px;
    color: #ccc;
  }

  .vue-accordion-component.draw-active .vue-accordion-title {
    color: #ff8000 !important;
  }

  .vue-accordion-icon {
    fill: #ccc;
  }

  .vue-accordion-content {
    background-color: #fff;
    font-size: 14px;
  }
</style>
