<template>
    <modal class="place-bet-modal" name="place-bet-modal" @before-open="beforeOpen" :height="getModalHeight()" :width="getModalWidth()">
        <form class="place-bet-modal__form" @submit.prevent="placeBet" action="" method="post">
            <!-- Step 1 -->
            <div v-if="state === 'BET'" class="place-bet-modal__step-1">
                <!-- Step 1: Payment -->
                <div class="step-1__payment">
                    <h2 class="step-title">How Many Tickets Are You Playing?</h2>
                    <div class="step-coin">
                        <img class="step-coin__image" :src="require('../assets/coins/' + this.buyTicker + '.png')" />
                        <h4 class="step-coin__name">{{ buyCoinName }}</h4>
                    </div>
                    <div class="step-ticket-price">{{ Number(buyPrice).toFixed(5) }} {{ this.buyTicker }}</div>
                    <div class="step-tickets-quantity">
                        <QuantityInput :minQty="minBuyAmount" />
                    </div>
                </div>

                <!-- Step 1: Prize -->
                <div class="step-1__prize">
                    <h2 class="step-title">Every Prize is Paid in BTC</h2>
                    <div class="step-coin">
                        <img class="step-coin__image" :src="require('../assets/coins/' + prizeTicker + '.png')" />
                        <h4 class="step-coin__name">{{ prizeCoinName }}</h4>
                    </div>
                    <h4 class="step-title">Please Provide Your BTC Address</h4>
                    <div class="step-payout-address">
                        <input value="" :placeholder="prizeTicker + ' Address for Prize Payout'" v-model="payoutAddress" />
                    </div>
                </div>

                <!-- Step 1: Buttons -->
                <div class="step-1__buttons">
                    <button class="button button--yellow button--next" type="submit" :disabled="invalidPayoutAddress()" :class="{ 'disabled': invalidPayoutAddress() }">Next</button>
                    <div class="button button--ghost button--cancel" @click="hide">Cancel</div>
                </div>
            </div>

            <!-- Step 2 -->
            <div v-if="state === 'CAPTCHA'" class="place-bet-modal__step-2">
                <!-- Step 2: Total Cost -->
                <div class="step-2__total-cost">
                    <h2 class="step-title">Total Cost</h2>
                    <div class="step-coin">
                        <img class="step-coin__image" :src="require('../assets/coins/' + this.buyTicker + '.png')" />
                        <h4 class="step-coin__name">{{ buyCoinName }}</h4>
                    </div>
                    <div class="step-text">{{ this.ticketCount * this.buyPrice }} {{ this.buyTicker }}</div>
                </div>

                <!-- Step 2: Prize Address -->
                <div class="step-2__prize-address">
                    <h2 class="step-title">Your Prize Address</h2>
                    <div class="step-coin">
                        <img class="step-coin__image" :src="require('../assets/coins/' + this.prizeTicker + '.png')" />
                        <h4 class="step-coin__name">{{ prizeCoinName }}</h4>
                    </div>
                    <div class="step-text">{{ this.payoutAddress }}</div>
                </div>

                <!-- Step 2: Are You Human -->
                <div class="step-2__captcha">
                    <h2 class="step-title">Are You Human?</h2>
                    <vue-recaptcha class="recaptcha" :sitekey="getRecaptchaSiteKey()" @verify="onVerify" @expired="onExpired"/>
                </div>

                <!-- Step 2: Buttons -->
                <div class="step-2__buttons">
                    <button class="button button--yellow button--next" :disabled="!captchaPass" :class="{ 'disabled': !captchaPass }">Confirm</button>
                    <div class="button button--ghost button--cancel" @click="back">Back</div>
                </div>
            </div>

            <!-- Step 3 -->
            <div v-if="state === 'PAY' && payinAddress" class="place-bet-modal__step-3">
                <!-- Step 3: Pay Address -->
                <div class="step-3__pay-address">
                    <h2 class="step-title">Pay To This Address</h2>
                    <div class="step-text">{{ this.payinAddress }}</div>
                </div>

                <!-- Step 3: Amount to Transfer -->
                <div class="step-3__amount-to-transfer">
                    <h2 class="step-title">Amount to Transfer for {{ ticketCount }} Tickets</h2>
                    <div class="step-text">{{ this.ticketCount * this.buyPrice }} {{ this.buyTicker }}</div>
                </div>

                <!-- Step 3: Notice -->
                <div class="step-3__notice">
                    <strong>NOTICE:</strong> A quick payment will increase the odds of securing this ticket price. Delayed payments may result in different conversion rates.
                </div>

                <!-- Step 3: Buttons -->
                <div class="step-3__buttons">
                    <div class="button button--ghost button--cancel" @click="hide">Close</div>
                </div>
            </div>
            <div v-if="state === 'PAY' && !payinAddress" class="place-bet-modal__step-3">
                <!-- Step 3: Error -->
                <div class="step-3__error">
                    <h2 class="step-title">Error</h2>
                    <div class="step-text">{{ warning || 'Something went wrong. Try again later.' }}</div>
                </div>

                <!-- Step 3: Buttons -->
                <div class="step-3__buttons">
                    <div class="button button--ghost button--cancel" @click="back">Back</div>
                </div>
            </div>

            <!-- Step - Loading -->
            <div v-if="state === 'WAITING'" class="place-bet-modal__step-loading" style="margin: 200px 0; text-align: center;">
              <!-- Step Loading: Message -->
                <div class="step-loading__message">
                    <div class="step-text">Processing request...</div>
                </div>
            </div>
        </form>
    </modal>
</template>

<script>
import Persistence from '@/persistence';
import QuantityInput from '@/components/QuantityInput';
import VueRecaptcha from 'vue-recaptcha';

export default {
  name: 'PlaceBetModal',
  props: {

  },
  components: {
    QuantityInput,
    VueRecaptcha
  },
  data() {
      return {
          state: 'BET',
          buyTicker: 'BTC',
          buyCoinName: '',
          buyPrice: '',
          prizeTicker: 'BTC',
          prizeCoinName: '',
          payoutAddress: '',
          ticketCount: 0,
          payinAddress: null,
          warning: null,
          captchaPass: false,
          minBuyAmount: 0,
      }
  },
  methods: {
    onVerify() {
      this.captchaPass = true;
    },
    onExpired() {
      this.captchaPass = false;
    },
    getRecaptchaSiteKey() {
        if (window.location.href.indexOf('localhost') >= 0) {
          return '6LeIxAcTAAAAAJcZVRqyHh71UMIEGNQ_MXjiZKhI';
        }
        return '6LcgdHwUAAAAAGGh4hrrOnXE0vnnYNGuQ6Z7igBG';
    },
    getModalWidth() {
      if (window.innerWidth < 768) {
        return '80%';
      } else if (window.innerWidth < 1200) {
        return '70%';
      }
      return '50%';
    },
    getModalHeight() {
      if (window.innerWidth < 768) {
        return '80%';
      } else if (window.innerWidth < 1200) {
        return 'auto';
      }
      return 'auto';
    },
    back() {
        if (this.state === 'CAPTCHA') {
          this.state = 'BET';
          this.captchaPass = false;
        } else if (this.state === 'PAY') {
          this.state = 'BET';
          this.captchaPass = false;
        }
    },
    hide() {
      this.$modal.hide('place-bet-modal');
    },
    beforeOpen(event) {
      this.state = 'BET';
      this.buyTicker = event.params.buyTicker;
      this.buyCoinName = event.params.buyCoinName
      this.minBuyAmount = event.params.minBuyAmount;
      this.buyPrice = event.params.buyPrice;
      this.prizeTicker = event.params.prizeTicker;
      this.prizeCoinName = event.params.prizeCoinName;
    },
    invalidPayoutAddress() {
      return !this.payoutAddress || this.payoutAddress === '';
    },
    placeBet() {
        if (this.state === 'BET') {
            if (this.invalidPayoutAddress()) {
              return;
            }
            this.ticketCount = document.getElementsByClassName('quantity-input')[0].value;
            this.state = 'CAPTCHA';
            return;
        }
        if (!this.captchaPass) {
          return;
        }
        this.state = 'WAITING';
        const args = {
            payout_address: this.payoutAddress,
            payout_ticker: this.prizeTicker,
            buyin_ticker: this.buyTicker,
            draw_ticker: this.prizeTicker,
            value: this.ticketCount * this.buyPrice
        };
        this.$api.placeBet(args, (paymentInfo) => {
            this.state = 'PAY';
            this.payinAddress = paymentInfo.address,
            this.value = paymentInfo.amount;

            if (paymentInfo.ticker) {
              this.buyTicker = paymentInfo.ticker;
            }

            this.warning = paymentInfo.warning;

            if (!this.warning) {
              Persistence.savePayoutAddress(args.payout_address);
            }



        }, () => {

        });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  /* For mobile: */
  .place-bet-modal__form {
    height: 100%;
    overflow-y: auto;
  }

  .place-bet-modal__step-1,
  .place-bet-modal__step-2 {
    display: flex;
    flex-flow: column;
    justify-content: flex-start;
    align-items: center;
  }

  .place-bet-modal__step-loading,
  .place-bet-modal__step-3 {
    display: flex;
    flex-flow: column;
    justify-content: flex-start;
    align-items: center;
  }

  .step-1__payment,
  .step-2__total-cost {
    width: 100%;
    padding: 20px;
    background-color: #ffffff;
  }

  .step-1__prize,
  .step-2__prize-address {
    width: 100%;
    padding: 20px;
    background-color: #f5f5dc;
  }

  .step-1__buttons,
  .step-2__buttons,
  .step-3__buttons {
    width: 100%;
    padding: 20px;
    background-color: #ffffff;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
  }

  .step-1__buttons .button,
  .step-2__buttons .button,
  .step-3__buttons .button {
    width: 100%;
    margin-bottom: 20px;
  }

  .step-1__buttons .button:last-child,
  .step-2__buttons .button:last-child,
  .step-3__buttons .button:last-child {
    margin-bottom: 0px;
  }

  .step-2__captcha {
    width: 100%;
    padding: 20px;
  }

  .step-loading__message {
    width: 100%;
    padding: 20px;
    background-color: #ffffff;
  }

  .step-3__pay-address,
  .step-3__amount-to-transfer,
  .step-3__buttons,
  .step-3__error {
    width: 100%;
    padding: 20px;
    background-color: #ffffff;
  }

  .step-3__notice {
    width: 100%;
    padding: 20px;
    background-color: #ffffff;
    max-width: 400px;
    text-align: center;
  }

  .step-title {
    text-align: center;
    margin: 0px 0px 20px;
  }

  .step-text {
    text-align: center;
    word-wrap:break-word;
  }

  .step-coin {
    text-align: center
  }

  .step-coin__image {
    width: 80px;
  }

  .step-coin__name {
    margin: 10px 0px 20px;
  }

  .step-ticket-price {
    text-align: center;
    margin-bottom: 10px;
  }

  .step-tickets-quantity {
    text-align: center;
  }

  .step-tickets-quantity input {
    width: 50px;
    text-align: center;
  }

  .step-payout-address {
    text-align: center;
  }

  .step-payout-address input {
    width: 220px;
  }

  /* For tablet: */
  @media only screen and (min-width: 768px) {
    .place-bet-modal__step-1,
    .place-bet-modal__step-2 {
      flex-flow: row wrap;
      align-items: flex-start;
    }

    .step-1__payment,
    .step-1__prize,
    .step-2__total-cost,
    .step-2__prize-address  {
      width: 50%;
      min-height: 300px;
    }

    .step-1__buttons,
    .step-2__buttons {
      display: flex;
      flex-flow: row nowrap;
      justify-content: space-between
    }

    .step-1__buttons .button,
    .step-2__buttons .button {
      margin-bottom: 0px;
      width: calc(50% - 10px);
    }

    .step-3__buttons .button {
      margin-bottom: 0px;
      width: 40%;
    }

    .step-1__buttons .button--cancel,
    .step-2__buttons .button--cancel {
      order: 1;
    }

    .step-1__buttons .button--next,
    .step-2__buttons .button--next {
      order: 2;
      cursor: pointer;
    }
  }

  .recaptcha {
    margin: 0 auto;
    width: 304px;
  }

  .button--next.disabled {
    opacity: 0.5;
    cursor: auto;
  }
</style>

<style>
  .v--modal-overlay {
    background: rgba(0, 0, 0, 0.5) !important;
  }
</style>
