<template>
  <BasePage>
    <div style="min-height: 1000px;">
      <api-request
        :resource="browse()"
        v-model="activeDraw"
        effect="fadeIn"
        spinner="PulseLoader"
        spinner-color="white"
        spinner-padding="150px"
        :spinner-scale="2">
        <HeroBanner :draw="activeDraw.data.draw" />
        <CoinGrid :markets="activeDraw.data.markets || []" />
      </api-request>
    </div>
  </BasePage>
</template>

<script>
import BasePage from '@/pages/BasePage.vue'
import CoinGrid from '@/components/CoinGrid.vue'
import HeroBanner from '@/components/HeroBanner.vue'

export default {
    name: 'Main',
    props: {

    },
    data () {
        return {
            activeDraw: {
                data: {
                    draw: {}
                }
            },
            updaterTimeout: null
        }
    },
    created: function () {
       this.drawUpdater();
    },
    beforeDestroy: function () {
      if (this.updaterTimeout) {
        clearTimeout(this.updaterTimeout);
      }
    },
    components: {
        BasePage,
        CoinGrid,
        HeroBanner
    },
    methods: {
        drawUpdater() {
          this.updaterTimeout = setTimeout(() => {
            this.$api.getActiveDraw().then((result) => this.activeDraw = result);
            this.drawUpdater();
          }, 60000);
        },
        browse () {
            return (params) => {
                return this.$api.getActiveDraw(params)
            }
        }
    },

}
</script>

