<template>
  <modal name="address-following-modal" @before-open="beforeOpen" :height="getModalHeight()" :width="getModalWidth()">
    <div class="address-following__content">
      <div class="address-following__payout-address">
        <input class="address-following__payout-address__input" type="text" placeholder="Payout Address" v-model="newAddress" />
        <input class="button button--yellow" type="button" value="Follow" @click="addAddress" />
      </div>

      <div class="payout-address-table">
        <div class="payout-address-table__header">
          <div class="payout-address__address">Payout Address</div>
          <div class="payout-address__action"></div>
        </div>
        <div class="payout-address-table__body">
          <div v-for="address in addresses" class="payout-address__item">
            <div class="payout-address-item__address">
              <span>{{ address }}</span>
            </div>
            <div class="payout-address-item__action" @click="removeAddress(address)">
              Remove
            </div>
          </div>
        </div>
      </div>
    </div>
  </modal>
</template>

<script>
import Persistence from '@/persistence';

export default {
  name: 'AddressFollowingModal',
  data() {
    return {
      addresses: [],
      newAddress: null
    }
  },
  methods: {
    getModalWidth() {
      if (window.innerWidth < 768) {
        return '80%';
      } else if (window.innerWidth < 1200) {
        return '50%';
      }
      return '40%';
    },
    getModalHeight() {
      if (window.innerWidth < 768) {
        return '60%';
      } else if (window.innerWidth < 1200) {
        return '50%';
      }
      return '40%';
    },
    hide () {
      this.$modal.hide('address-following-modal');
    },
    beforeOpen () {
      this.addresses = Persistence.getPayoutAddresses();
    },
    addAddress() {
      if (this.newAddress && this.newAddress !== '') {
        Persistence.savePayoutAddress(this.newAddress);
        this.addresses = Persistence.getPayoutAddresses();
        this.newAddress = '';
      }
    },
    removeAddress(address) {
      if (address) {
        Persistence.removePayoutAddress(address);
        this.addresses = Persistence.getPayoutAddresses();
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  /* For mobile: */
  .address-following__content {
    height: 100%;
    padding: 20px 10px;
  }

  .address-following__payout-address__input {
    width: 100%;
  }

  .address-following__payout-address .button {
    width: 100%;
    margin: 10px 0px;
    padding: 5px 40px;
    line-height: normal;
  }

  .payout-address-table {
    height: calc(100% - 53px);
    font-size: 12px;
  }

  .payout-address-table__header {
    font-weight: 700;
  }

  .payout-address-table__body {
    height: calc(100% - 27px);
    overflow-y: scroll;
  }

  .payout-address-table__header,
  .payout-address__item {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;
    padding: 5px;
  }

  .payout-address__item:nth-child(odd) {
    background-color: #eee;
  }

  .payout-address__item.green-background {
    background-color: #7dcea0;
    font-weight: 700;
  }

  .payout-address__address,
  .payout-address-item__address {
    width: 70%;
    text-align: left;
  }

  .payout-address-item__address span {
    display: inline-block;
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding-right: 3px;
  }

  .payout-address__action,
  .payout-address-item__action {
    width: 30%;
    text-align: right;
  }

  .payout-address-item__action {
    font-weight: 700;
    cursor: pointer;
  }

  /* For tablet: */
  @media only screen and (min-width: 768px) {
    .address-following__payout-address {
      display: flex;
      flex-flow: row nowrap;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 20px;
    }

    .address-following__payout-address__input {
      width: 60%;
      margin: 0px 5px 0px 0px;
    }

    .address-following__payout-address .button {
      width: 40%;
      margin: 0px 0px 0px 5px;
    }
  }
</style>
