import axios from 'axios'
import qs from 'qs';

import Persistence from '@/persistence';

//const host = 'http://localhost:8081/api';
const host = 'http://api.betcoin777.com/api';

const getPayoutAddresses = () => {
  return Persistence.getPayoutAddresses().join(',');
};

export default {
    getGeoCheck: () => axios.get(host + '/geocheck.php'),
    getActiveDraw: () => axios.get(host + '/activedraw.php'),
    getDrawHistory: () => axios.get(host + '/drawhistory.php'),
    getDrawDetails: (drawId) => axios.get(host + '/drawdetails.php?draw_id=' + drawId),
    getMyBets: () => axios.get(host + '/mybets.php?addresses=' + getPayoutAddresses()),
    getUnpaidInvoices: () => axios.get(host + '/unpaidinvoices.php?addresses=' + getPayoutAddresses()),
    getMyPayments: () => axios.get(host + '/mypayments.php?addresses=' + getPayoutAddresses()),
    getMyLeftOvers: () => axios.get(host + '/myleftoverPayments.php?addresses=' + getPayoutAddresses()),
    getTickets: (addressId, drawId) => axios.get(host + '/ticketsbyaddress.php?address_id=' + addressId + '&draw_id=' + drawId),
    placeBet: (args, onSuccess, onError) => {
        axios.post(host + '/placebet.php', qs.stringify(args), { headers: { 'Content-Type': 'application/x-www-form-urlencoded' } })
        .then((response) => {
            if (response.status === 200) {
              const data = response.data || {};
              onSuccess({
                  'address': data.payment_address,
                  'amount': data.value,
                  'ticker': data.ticker,
                  'warning': data.warning,
              });
              return;
            }
            onError();
        })
        .catch((/*error*/) => {
            alert('Error');
            onError();
            // TODO: handle error
        });
    }
}
