<template>
    <div class="quantity">
        <div v-if="currentQty == minQty" class="decrease-quantity disabled" title="Decrease Quantity">
          <font-awesome-icon icon="minus" />
        </div>
        <div v-if="currentQty != minQty" class="decrease-quantity" title="Decrease Quantity" @click="decrease()">
          <font-awesome-icon icon="minus" />
        </div>
        <input class="quantity-input" v-model="currentQty" readonly />
        <div class="increase-quantity" title="Increase Quantity" @click="increase()">
          <font-awesome-icon icon="plus" />
        </div>
    </div>
</template>

<script>
export default {
  name: 'QuantityInput',
  props: {
    minQty: Number
  },
  data() {
      return {
        currentQty: this.minQty
      }
  },
  methods: {
    decrease() {
      this.currentQty = Math.max(this.currentQty - 1, this.minQty);
    },
    increase() {
       this.currentQty++;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .quantity {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
  }

  .quantity input {
    width: 50px;
    text-align: center;
  }

  .decrease-quantity,
  .increase-quantity {
    font-size: 16px;
    margin: 0 10px;
    cursor: pointer;
  }

  .decrease-quantity.disabled {
    opacity: 0.5;
  }
</style>
