/* globals localStorage */

export default {
    savePayoutAddress(address) {
      let stored = localStorage.addresses ? JSON.parse(localStorage.addresses) : {};
      if (!stored) {
        stored = {};
      }
      stored[address] = true;
      localStorage.addresses = JSON.stringify(stored);
    },

    getPayoutAddresses() {
      return localStorage.addresses ? Object.keys(JSON.parse(localStorage.addresses)) : [];
    },

    removePayoutAddress(address) {
      const stored = JSON.parse(localStorage.addresses);
      if (stored && stored[address]) {
        delete stored[address];
        localStorage.addresses = JSON.stringify(stored);
      }
      return false;
    }
}
