<template>
  <div class="coin-grid" v-if="markets && markets.length > 0">
    <div class="coin-grid__header">
      <h2 class="coin-grid__title">
        Choose Coin & Buy Tickets
      </h2>
      <input class="coin-grid__search" type="text" placeholder="Find Your Favourite Coin" v-model="search" style="top: 7px; position: relative;" />
    </div>

    <div class="coin-grid__items">
      <div class="coin-grid__item" v-for="(item, index) in getFilteredMarkets()">
        <CoinCard
              :name="item.coin_name"
              :ticker="item.ticker"
              :ticketPrice="item.ticket_price"
              :minTickets="item.min_tickets" />
      </div>
    </div>
  </div>
</template>

<script>
import CoinCard from '../components/CoinCard.vue'

export default {
  name: 'CoinGrid',
  data() {
    return {
      search: ''
    }
  },
  props: {

      markets: Array
  },
  components: {
    CoinCard
  },
  methods: {
    getFilteredMarkets() {
      if (!this.search || this.search === '') {
        return this.markets;
      }
      let filteredMarkets = [];
      let market;
      for(let i = 0; i < this.markets.length; ++i) {
        market = this.markets[i];
        if (market.ticker.toLowerCase().indexOf(this.search.toLowerCase()) >= 0 || market.coin_name.toLowerCase().indexOf(this.search.toLowerCase()) >= 0) {
          filteredMarkets.push(market);
        }
      }
      return filteredMarkets;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  /* For mobile: */
  .coin-grid__header {
    display: flex;
    flex-flow: column;
    justify-content: flex-start;
    margin-bottom: 20px;
  }

  .coin-grid__title {
    position: relative;
    text-align: left;
    z-index: 0;
  }

  .coin-grid__items {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    min-height: 800px;
  }

  .coin-grid__item {
    width: 100%;
    margin: 0px 0px 30px;
  }

  /* For tablet: */
  @media only screen and (min-width: 768px) {
    .coin-grid__header {
      flex-flow: row nowrap;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 30px;
    }

    .coin-grid__title {
      margin: 0px;
    }

    .coin-grid__item {
      width: calc(50% - 1.5rem);
      margin: 0px 0px 40px;
    }
  }

  /* For desktop: */
  @media only screen and (min-width: 1200px) {
    .coin-grid__header {
      margin-bottom: 40px;
    }

    .coin-grid__items {
    justify-content: flex-start;
  }

    .coin-grid__item {
      width: calc(33.33% - 2rem);
      margin: 0px 0px 50px;
    }

    .coin-grid__item:nth-child(3n+2) {
      margin: 0px 3rem 50px;
    }
  }
</style>
