<template>
    <api-request
      :resource="load()"
      v-model="leftovers"
      effect="fadeIn"
      spinner="PulseLoader"
      spinner-color="white"
      spinner-padding="150px"
      :spinner-scale="2">
      <NoResults :visible="((leftovers.data || {}).addresses || []).length === 0"/>
      <div v-if="((leftovers.data || {}).addresses || []).length > 0">
        <div class="table">
          <div class="table__header">
            <div class="table__header__address">Address</div>
            <div class="table__header__balance">Available Balance</div>
          </div>
          <div class="table__body">
            <div v-for="address in (leftovers.data || {}).addresses" class="table__body-item">
              <div v-for="leftover in (leftovers.data || {}).leftOverPayments[address.address_id]" class="table__body-item-wrapper">
                <div class="table__body-item__address">
                  <img :src="getCoinLogo(address.payout_ticker)" :alt="address.payout_ticker">
                  <span>{{ address.payout_address }}</span>
                </div>
                <div class="table__body-item__balance">
                  {{ leftover.amount }} {{ leftover.ticker }}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </api-request>
</template>

<script>
import NoResults from '@/components/NoResults.vue'

export default {
  name: 'LeftOverTab',
  props: {

  },
  data() {
    return {
      leftovers: {},
    }
  },
  components: {
    NoResults
  },
  methods: {
    getCoinLogo: (ticker) => {
      return require('../assets/coins/' + ticker + '.png')
    },
    load () {
        return (params) => this.$api.getMyLeftOvers(params)
    }
  }
}
</script>

<style scoped>
  /* For mobile: */
  .table {
    font-size: 12px;
  }

  .table__header {
    font-size: 15px;
    background-color: #393731;
    color: #ccc;
  }

  .table__body-item {
    background-color: #fff;
  }

  .table__body-item:nth-child(even) {
    background-color: #eee;
  }

  .table__header,
  .table__body-item-wrapper {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;
    padding: 15px;
  }

  .table__header__address,
  .table__body-item__address {
    width: 70%;
  }

  .table__body-item__address {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;
  }

  .table__body-item__address img {
    height: 13px;
    margin-right: 3px;
  }

  .table__body-item__address span {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding-right: 3px;
  }

  .table__header__balance,
  .table__body-item__balance {
    width: 30%;
    padding-left: 10px;
    text-align: right;
  }

  /* For tablet: */
  @media only screen and (min-width: 768px) {
    .table__header,
    .table__body-item-wrapper {
      padding: 15px 20px;
    }
  }
</style>
