import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/pages/Main'
import History from '@/pages/History'
import MyBets from '@/pages/MyBets'
import DrawDetails from '@/pages/DrawDetails'
import Rules from '@/pages/Rules'
import Legal from '@/pages/Legal'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'main',
      component: Main
    },
    {
      path: '/history',
      name: 'history',
      component: History
    },
    {
      path: '/my-bets',
      name: 'my-bets',
      component: MyBets
    },
    {
      path: '/draw-details/:drawId',
      name: 'draw-details',
      component: DrawDetails
    },
    {
      path: '/rules',
      name: 'rules',
      component: Rules
    },
    {
      path: '/legal',
      name: 'legal',
      component: Legal
    }
  ]
})
