<template>
  <div class="hero-banner gradient">
    <div class="hero-banner__title">
      Crypto Millions
    </div>
    <div class="hero-banner__subtitle">
      Find Your Crypto Luck Here
    </div>
    <div class="box-grid">
      <div class="box-grid__item">
        <Box number="1" title="Choose Coin" content="Pick the coin you want to bet with."/>
      </div>
      <div class="box-grid__item">
        <Box number="2" title="Buy Tickets" content="Place a bet by sending the ticket price to your lottery account."/>
      </div>
      <div class="box-grid__item">
        <Box number="3" title="Collect Prize" content="When you win the lottery, the prize will be sent back to your account."/>
      </div>
    </div>
    <div class="learn-more-warapper">
      <router-link to="rules" class="learn-more">
        Learn More
      </router-link>
    </div>
    <div class="hero-banner__bottom" v-if="!draw.draw_id">
      <div class="hero-banner__bottom__side-info" style="width: 50%; margin: 0">
        Waiting For Next Draw
        <Loader :loading="true" color="#fff" style="float: left; position: absolute; top: 6px; left: 40px;"/>
        <Loader :loading="true" color="#fff" style="float: right; position: absolute; top: 6px; right: 40px;"/>
      </div>
    </div>
    <div class="hero-banner__bottom" v-if="draw.draw_id">
      <div class="hero-banner__bottom__side-info">
        {{ new Number(draw.prize_pool).toFixed(4) }} {{ draw.std_coin_ticker }}
      </div>
      <div class="hero-banner__bottom__main-info">
        <p>Current Draw</p>
        <p>
            <strong>{{ draw.metronome_ticker }} #{{ draw.metronome_winner_height }}</strong>
        </p>
      </div>
      <div class="hero-banner__bottom__side-info">
        <span v-if="countdownIsEnd">Soon!</span>
        <span class="blocks-left">
          <strong>{{ draw.metronome_winner_height - draw.current_block_height }} </strong>
          <span v-if="draw.metronome_winner_height - draw.current_block_height !== 1">blocks </span>
          <span v-if="draw.metronome_winner_height - draw.current_block_height === 1">block</span>
          left
        </span>
        <countdown v-if="!countdownIsEnd" :time="estimatedTimeMissing()" @progress="handleProgress">
            <template slot-scope="props">
                {{ formatDigit(props.totalHours) }}:{{ formatDigit(props.minutes) }}:{{ formatDigit(props.seconds) }}
            </template>
        </countdown>
      </div>
    </div>
  </div>
</template>

<script>
import Box from '@/components/Box.vue'
import Loader from 'vue-spinner/src/ScaleLoader.vue'

export default {
  name: 'HeroBanner',
  data() {
      return {
          countdownIsEnd: false
      };
  },
  props: {
    draw: Object
  },
  components: {
    Box,
    Loader
  },
  methods: {
      formatDigit(digits) {
          return digits < 10 ? '0' + digits : digits;
      },
      estimatedTimeMissing() {
          return (this.draw.metronome_winner_height - this.draw.current_block_height) * 600000
      },
      handleProgress(data) {
          this.countdownIsEnd = data.totalSeconds < 3600;
      }
  }
}
</script>

<style scoped>
  /* For mobile: */
  .hero-banner {
    position: relative;
    margin: 40px 0px;
    background-color: #ffffff;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.2);
    opacity: 0.8;
  }

  .blocks-left {
    font-size: 13px;
    position: absolute;
    bottom: 5px;
    line-height: 11px;
    left: 0;
    right: 0;
  }

  .gradient {
    background: #f6f8f9; /* Old browsers */
    background: -moz-linear-gradient(top, #f6f8f9 0%, #d7dee3 80%, #f5f7f9 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(top, #f6f8f9 0%,#d7dee3 80%,#f5f7f9 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, #f6f8f9 0%,#d7dee3 80%,#f5f7f9 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f6f8f9', endColorstr='#f5f7f9',GradientType=0 ); /* IE6-9 */
  }

  .hero-banner__title {
    font-size: 50px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    padding-top: 40px;
  }

  .hero-banner__subtitle {
    font-size: 18px;
    font-weight: 700;
    text-align: center;
    margin-top: 10px;
  }

  .box-grid {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    margin: 35px 20px 15px;
  }

  .box-grid__item {
    width: 100%;
    margin: 10px 0px;
  }

  .hero-banner__bottom {
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    padding: 10px 0px;
    background-color: #141414;
  }

  .hero-banner__bottom__side-info {
    font-size: 36px;
    line-height: 75px;
    text-align: center;
    color: #ffffff;
    background-color: rgba(255,255,255,0.1);
    width: 200px;
    margin: 10px 0px;
    border-radius: 2px;
    position: relative;
  }

  .hero-banner__bottom__main-info {
    line-height: 12px;
    text-align: center;
    color: #ffffff;
    background-color: #ff8000;
    width: 125px;
  }

  .learn-more-warapper {
    text-align: center;
    text-align: center;
    margin-top: 20px;
  }

  .learn-more {
    transition: all 0.1s;
    cursor: pointer;
    text-transform: uppercase;
  }

  .learn-more:hover {
    color: #ff8000;
  }

  /* For tablet: */
  @media only screen and (min-width: 768px) {
    .hero-banner {
      height: 600px;
    }

    .box-grid {
      flex-flow: row nowrap;
      justify-content: space-between;
      margin: 35px 10px 0px;
    }

    .box-grid__item {
      width: 33.33%;
      margin: 0px 5px;
    }

    .hero-banner__bottom {
      position: absolute;
      left: 0px;
      bottom: 0px;
      width: 100%;
      height: 25%;
      flex-flow: row nowrap;
    }

    .hero-banner__bottom__side-info {
      margin: 10px;
    }
  }

  /* For other tablet size: */
  @media only screen and (min-width: 900px) {
    .box-grid {
      margin: 35px auto 0px;
      max-width: 750px;
    }
  }

  /* For desktop: */
  @media only screen and (min-width: 1200px) {
    .hero-banner {
      margin: 60px 0px;
    }

    .box-grid {
      max-width: 900px;
    }

    .box-grid__item {
      margin: 0px 20px;
    }
  }
</style>
