<template>
  <BasePage>
    <div class="app__page-title">
      Draw Details
    </div>
    <api-request
      :resource="load()"
      v-model="drawDetails"
      effect="fadeIn"
      spinner="PulseLoader"
      spinner-color="white"
      spinner-padding="150px"
      :spinner-scale="2">
      <Accordion v-for="(item, index) in [drawDetails.data]" :items="[item]" :selectors="{ id: 'id', title: 'title', content: 'content' }" :active="index === 0 ? [0] : []" :collapseAll="false" ref="accordion">
        <template slot="title" slot-scope="{ item }">
          <div class="draw-header">
            <div class="draw-info">
              <div class="draw-info__item" title="Draw Number">
                <font-awesome-icon icon="hashtag" />
                <span>{{ item.draw_id }}</span>
              </div>
              <div class="draw-info__item" title="Draw Date">
                <font-awesome-icon icon="calendar-alt" />
                <span>{{ item.time_to_end }}</span>
              </div>
              <div class="draw-info__item" title="Total Tickets Bought">
                <font-awesome-icon icon="ticket-alt" />
                <span>{{ item.tickets }}</span>
              </div>
              <div class="draw-info__item" title="Prize Pool">
                <font-awesome-icon icon="gem" />
                <span>{{ new Number(item.prize_pool).toFixed(4) }} {{ item.ticker }}</span>
              </div>
              <div class="draw-info__item show-for-large" title="Metronome Winning Hash">
                <font-awesome-icon icon="cubes" />
                <span v-if="item.current_block_height < item.metronome_winner_height">Waiting for Block #{{ item.metronome_winner_height }}</span>
                <span v-if="item.current_block_height >= item.metronome_winner_height">0x{{ item.metronome_winner_hash }}</span>
                <span style="color: #ff8000; font-size: 11px;" v-if="item.current_block_height - item.metronome_winner_height < 3 && item.current_block_height - item.metronome_winner_height >= 0">
                  confirming
                </span>
              </div>
            </div>
            <div class="draw-header-angle-up">
              <font-awesome-icon icon="angle-up" />
            </div>
            <div class="draw-header-angle-down">
              <font-awesome-icon icon="angle-down" />
            </div>
          </div>
        </template>
        <template slot="content" slot-scope="{ item }">
            <div class="draw-winning-hash hide-for-large" v-if="item.current_block_height < item.metronome_winner_height">
                <span class="draw-winning-hash__label">Winning Hash: </span>
                <span class="draw-winning-hash__value">Waiting for Block #{{ item.metronome_winner_height }}</span>
              </div>

              <div class="draw-winning-hash hide-for-large" v-if="item.current_block_height >= item.metronome_winner_height">
                <span class="draw-winning-hash__label">Winning Hash: </span>
                <span class="draw-winning-hash__value">0x{{ item.metronome_winner_hash }}</span>
                <span style="color: #ff8000; font-size: 11px;" v-if="item.current_block_height - item.metronome_winner_height < 3 && item.current_block_height - item.metronome_winner_height >= 0">
                  confirming
                </span>
              </div>

            <div class="draw-table">
              <div class="draw-table__header">
                <div class="draw-table__address">Address</div>
                <div class="draw-table__tickets">Tickets</div>
              </div>
              <div class="draw-table__bettors-list">
                <div class="draw-table__bettor" v-for="ticket in item.tickets_map">
                  <div class="draw-bettor__address">
                    <img :src="getCoinLogo(ticket.payout_ticker)" :alt="ticket.payout_ticker">
                    <span>{{ ticket.payout_address }}</span>
                  </div>
                  <div class="draw-bettor__tickets">
                    <span class="draw-bettor__tickets-number">{{ ticket.tickets_number }}</span>

                    <div class="draw-bettor__tickets-details" title="Show Tickets" @click="showTicketsModal(ticket.address_id, item.draw_id, ticket.payout_address)">
                      <font-awesome-icon icon="search" />
                    </div>
                  </div>
                  </div>
                </div>
              </div>
            </div>
        </template>
      </Accordion>
    </api-request>

    <div style="height: 850px" />
  </BasePage>
</template>

<script>
import BasePage from '@/pages/BasePage.vue'
import Accordion from '@profesia/vue-accordion-component'

export default {
  name: 'DrawDetails',
  components: {
    BasePage,
    Accordion
  },
  data() {
    return {
      drawDetails: {}
    }
  },
  methods: {
    getCoinLogo: (ticker) => {
      return require('../assets/coins/' + ticker + '.png')
    },
    showTicketsModal (addressId, drawId, payoutAddress) {
      this.$modal.show('tickets-modal', {
        addressId: addressId,
        drawId: drawId,
        payoutAddress: payoutAddress
      });
    },
    load () {
        return (params) => this.$api.getDrawDetails(this.$route.params.drawId)
    }
  }
}
</script>

<style scoped>
  .app__page-title {
    padding: 40px 0 20px;
    font-size: 18px;
    font-weight: 700;
  }

  .draw-header {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
  }

  .draw-info {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
  }

  .draw-info__item {
    margin-right: 20px;
  }

  .draw-info__item svg {
    margin-right: 5px;
  }

  .draw-header-angle-up {
    display: none;
  }

  .draw-winning-hash {
    margin-bottom: 20px;
    text-align: center;
  }

  .draw-winning-hash__label {
    font-size: 12px;
    font-weight: 700;
  }

  .draw-winning-hash__value {
    font-size: 12px;
    word-wrap:break-word;
  }

  .draw-table {
    font-size: 12px;
  }

  .draw-table__header {
    font-weight: 700;
  }

  .draw-table__header,
  .draw-table__bettor {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;
    padding: 5px;
  }

  .draw-table__bettor:nth-child(odd) {
    background-color: #eee;
  }

  .draw-table__address,
  .draw-bettor__address {
    width: 80%;
  }

  .draw-bettor__address {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;
  }

  .draw-bettor__address img {
    height: 13px;
    margin-right: 3px;
  }

  .draw-bettor__address span {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding-right: 3px;
  }

  .draw-table__tickets,
  .draw-bettor__tickets {
    width: 20%;
    padding-left: 10px;
    text-align: right;
  }

  .draw-bettor__tickets {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-end;
    align-items: center;
  }

  .draw-bettor__tickets-details {
    margin-left: 5px;
    transition: color 0.3s ease;
  }

  .draw-bettor__tickets-details:hover {
    color: #ff8000;
    cursor: pointer;
  }
</style>

<style>
  .vue-accordion-item {
    border: 0 !important;
    margin-bottom: 5px;
  }

  .vue-accordion-item-active {
    border: 0 !important;
  }

  .vue-accordion-item-active .draw-header-angle-up {
    display: block;
  }

  .vue-accordion-item-active .draw-header-angle-down {
    display: none;
  }

  .vue-accordion-title {
    background-color: #393731 !important;
    padding: 10px 15px !important;
    font-size: 14px;
    color: #ccc;
  }

  .vue-accordion-icon {
    fill: #ccc;
  }

  .vue-accordion-content {
    background-color: #fff;
    font-size: 14px;
  }
</style>
