<template>
    <api-request
      :resource="load()"
      v-model="betInfo"
      effect="fadeIn"
      spinner="PulseLoader"
      spinner-color="white"
      spinner-padding="150px"
      :spinner-scale="2">
      <NoResults :visible="((betInfo.data || {}).draws || []).length === 0"/>
      <Accordion v-for="(item, index) in (betInfo.data || {}).draws || []" :items="[item]" :selectors="{ id: 'id', title: 'title', content: 'content' }" :active="index === 0 || index === 1 ? [0] : []" :collapseAll="false" ref="accordion">
        <template slot="title" slot-scope="{ item }">
          <div class="draw-header">
            <div class="draw-info">
              <div class="draw-info__item" title="Draw Number">
                <font-awesome-icon icon="hashtag" />
                <span>{{ item.draw_id }}</span>
              </div>
              <div class="draw-info__item" title="Draw Date">
                <font-awesome-icon icon="calendar-alt" />
                 <span>{{ new Date(item.utime * 1000).toLocaleDateString("en-US") }}</span>
              </div>
              <div class="draw-info__item" title="Total Tickets Bought">
                <font-awesome-icon icon="ticket-alt" />
                <span>{{ item.tickets }}</span>
              </div>
              <div class="draw-info__item" title="Prize Pool">
                <font-awesome-icon icon="gem" />
                <span>{{ item.prize_pool }} {{ item.std_coin_ticker }}</span>
              </div>
              <div class="draw-info__item show-for-large" title="Metronome Winning Hash">
                <font-awesome-icon icon="cubes" />
                <span v-if="item.current_block_height < item.metronome_winner_height">Waiting for Block #{{ item.metronome_winner_height }}</span>
                <span v-if="item.current_block_height >= item.metronome_winner_height">0x{{ item.metronome_winner_hash }}</span>
                <span style="color: #ff8000; font-size: 11px;" v-if="item.current_block_height - item.metronome_winner_height < 3 && item.current_block_height - item.metronome_winner_height >= 0">
                  confirming
                </span>
              </div>
            </div>
            <div class="draw-header-angle-up">
              <font-awesome-icon icon="angle-up" />
            </div>
            <div class="draw-header-angle-down">
              <font-awesome-icon icon="angle-down" />
            </div>
          </div>
        </template>
        <template slot="content" slot-scope="{ item }">
            <div class="draw-winning-hash hide-for-large" v-if="item.current_block_height < item.metronome_winner_height">
              <span class="draw-winning-hash__label">Winning Hash: </span>
              <span class="draw-winning-hash__value">Waiting for Block #{{ item.metronome_winner_height }}</span>
            </div>

            <div class="draw-winning-hash hide-for-large" v-if="item.current_block_height >= item.metronome_winner_height">
              <span class="draw-winning-hash__label">Winning Hash: </span>
              <span class="draw-winning-hash__value">0x{{ item.metronome_winner_hash }}</span>
              <span style="color: #ff8000; font-size: 11px;" v-if="item.current_block_height - item.metronome_winner_height < 3 && item.current_block_height - item.metronome_winner_height >= 0">
                confirming
              </span>
            </div>

            <div class="draw-table">
              <div class="draw-table__header">
                <div class="draw-table__address">Address</div>
                <div class="draw-table__tickets">Tickets</div>
                <div class="draw-table__prize-amount">Prize</div>
                <div class="draw-table__status">Status</div>
              </div>
              <div class="draw-table__results">
                <div v-for="addrEntry in (betInfo.data || {}).tickets[item.draw_id]" class="draw-table__prize" :class="{ 'green-background': addrEntry.counts > 0 }">
                  <div class="draw-prize__address">
                    <img :src="getCoinLogo((betInfo.data || {}).addresses[addrEntry.address_id].payout_ticker)" :alt="(betInfo.data || {}).addresses[addrEntry.address_id].payout_ticker">
                    <span>{{ (betInfo.data || {}).addresses[addrEntry.address_id].payout_address }}</span>
                  </div>
                  <div class="draw-prize__tickets">
                    {{ addrEntry.tickets_no }}
                    <div class="draw-prize__tickets-details" title="Show Tickets" @click="showTicketsModal(addrEntry.address_id, item.draw_id, (betInfo.data || {}).addresses[addrEntry.address_id].payout_address)">
                      <font-awesome-icon icon="search" />
                    </div>
                  </div>
                  <div class="draw-prize__prize-amount">
                    <span v-if="hasPayment(item, addrEntry)">
                      {{ getTotalPayment(item, addrEntry) }}
                      {{ betInfo.data.prizePaymentDatas[item.draw_id][addrEntry.address_id].prize_ticker }}
                    </span>
                    <span v-if="!hasPayment(item, addrEntry)">
                      -
                    </span>
                  </div>
                  <div class="draw-prize__status" v-if="hasPayment(item, addrEntry) && betInfo.data.prizePaymentDatas[item.draw_id][addrEntry.address_id].confirmations >= 3">
                    <font-awesome-icon icon="check" />
                  </div>
                  <div class="draw-prize__status" v-else-if="hasPayment(item, addrEntry) && betInfo.data.prizePaymentDatas[item.draw_id][addrEntry.address_id].confirmations >= 0 && betInfo.data.prizePaymentDatas[item.draw_id][addrEntry.address_id].confirmations < 3">
                    <font-awesome-icon icon="clock" />
                    &nbsp; {{ betInfo.data.prizePaymentDatas[item.draw_id][addrEntry.address_id].confirmations }}/3
                  </div>
                  <div class="draw-prize__status" v-else-if="hasPayment(item, addrEntry) && betInfo.data.prizePaymentDatas[item.draw_id][addrEntry.address_id].confirmations < 0">
                    <font-awesome-icon icon="times" />
                  </div>
              </div>
              </div>
            </div>
        </template>
      </Accordion>
    </api-request>
</template>

<script>
import BasePage from '@/pages/BasePage.vue'
import Accordion from '@profesia/vue-accordion-component'
import NoResults from '@/components/NoResults.vue'

export default {
  name: 'TicketsTab',
  props: {

  },
  data() {
    return {
      betInfo: {
        data: {

        }
      },
    }
  },
  components: {
    BasePage,
    Accordion,
    NoResults
  },
  methods: {
    getCoinLogo: (ticker) => {
      return require('../assets/coins/' + ticker + '.png')
    },
    hasPayment (item, addrEntry) {
      if (!this.betInfo.data.prizePaymentDatas[item.draw_id] || !this.betInfo.data.prizePaymentDatas[item.draw_id][addrEntry.address_id]) {
        return false;
      }
      return true;
    },
    getTotalPayment (item, addrEntry) {
      if (!this.hasPayment(item, addrEntry)) {
        return 0;
      }
      return new Number(this.betInfo.data.prizePaymentDatas[item.draw_id][addrEntry.address_id].total_prize).toFixed(8);
    },
    load () {
        return (params) => this.$api.getMyBets(params)
    },
    showTicketsModal (addressId, drawId, payoutAddress) {
      this.$modal.show('tickets-modal', {
        addressId: addressId,
        drawId: drawId,
        payoutAddress: payoutAddress
      });
    }
  }
}
</script>

<style scoped>
  /* For mobile: */
  .app__page-title {
    padding: 40px 0 20px;
    font-size: 18px;
    font-weight: 700;
  }

  .draw-header {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
  }

  .draw-info {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
  }

  .draw-info__item {
    margin-right: 20px;
    margin-bottom: 5px;
  }

  .draw-info__item svg {
    margin-right: 5px;
  }

  .draw-header-angle-up {
    display: none;
  }

  .draw-winning-hash {
    margin-bottom: 20px;
    text-align: center;
  }

  .draw-winning-hash__label {
    font-size: 12px;
    font-weight: 700;
  }

  .draw-winning-hash__value {
    font-size: 12px;
    word-wrap:break-word;
  }

  .draw-table {
    font-size: 12px;
  }

  .draw-table__header {
    font-weight: 700;
  }

  .draw-table__header,
  .draw-table__prize {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;
    padding: 5px;
  }

  .draw-table__prize:nth-child(odd) {
    background-color: #eee;
  }

  .draw-table__prize.green-background {
    background-color: #7dcea0;
    font-weight: 700;
  }

  .draw-table__address,
  .draw-prize__address {
    width: 40%;
  }

  .draw-prize__address {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;
  }

  .draw-prize__address img {
    height: 13px;
    margin-right: 3px;
  }

  .draw-prize__address span {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding-right: 3px;
  }

  .draw-table__tickets,
  .draw-prize__tickets {
    width: 20%;
    padding-left: 10px;
    text-align: right;
  }

  .draw-prize__tickets {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-end;
  }

  .draw-prize__tickets-details {
    margin-left: 5px;
    transition: color 0.3s ease;
  }

  .draw-prize__tickets-details:hover {
    color: #ff8000;
    cursor: pointer;
  }

  .draw-table__prize-amount,
  .draw-prize__prize-amount {
    width: 20%;
    padding-left: 10px;
    text-align: right;
  }

  .draw-table__status,
  .draw-prize__status {
    width: 20%;
    padding-left: 10px;
    text-align: right;
  }

  /* For tablet: */
  @media only screen and (min-width: 768px) {
    .draw-table__address,
    .draw-prize__address {
      width: 60%;
    }

    .draw-table__tickets,
    .draw-prize__tickets {
      width: 10%;
    }

    .draw-table__status,
    .draw-prize__status {
      width: 10%;
    }
  }
</style>

<style>
  .vue-accordion-item {
    border: 0 !important;
    margin-bottom: 5px;
  }

  .vue-accordion-item-active {
    border: 0 !important;
  }

  .vue-accordion-item-active .draw-header-angle-up {
    display: block;
  }

  .vue-accordion-item-active .draw-header-angle-down {
    display: none;
  }

  .vue-accordion-title {
    background-color: #393731 !important;
    padding: 10px 15px !important;
    font-size: 14px;
    color: #ccc;
  }

  .vue-accordion-icon {
    fill: #ccc;
  }

  .vue-accordion-content {
    background-color: #fff;
    font-size: 14px;
  }
</style>
