<template>
  <div class="splash" :style="{'display': hidden ? 'none' : 'block'}">
    <img class="pulse" :src="require('../assets/clover.png')" width="10%" />
  </div>
</template>

<script>
export default {
  name: 'Splash',
  data() {
    return {
      hidden: false
    }
  },
  mounted() {
    this.hide();
  },
  methods: {
    hide() {
      setTimeout(() => {
        this.hidden = true;
      }, 1000);
    }
  }
}
</script>

<style scoped>
  .splash {
    position: fixed;
    top: 0px;
    width: 100vw;
    height: 100vh;
    z-index: 2;
    background-color: #ffffff;
  }

  img {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
  }

  .pulse {
    animation: pulse 0.3s infinite;
    display: table;
    animation-direction: alternate;
    -webkit-animation-name: pulse;
    animation-name: pulse;
  }

  @-webkit-keyframes pulse {
    0% {
      -webkit-transform: scale(1);
    }
    50% {
      -webkit-transform: scale(1.1);
    }
    100% {
      -webkit-transform: scale(1);
    }
  }

  @keyframes pulse {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.1);
    }
    100% {
      transform: scale(1);
    }
  }

</style>
