<template>
  <div class="coin-card">
    <div class="coin-card__wrapper">
      <div class="coin-card__main">
        <div class="coin-card__logo">
          <img :src="require('../assets/coins/' + this.ticker + '.png')" />
        </div>
        <div class="coin-card__name">
          {{ name }}
        </div>
      </div>

      <div class="coin-card__details">
        <div class="coin-card__market">
          <div class="coin-card__price">
            <div class="coin-card__price-label">
              Ticket price
            </div>

            <div class="coin-card__price-value">
               <strong>{{ Number(ticketPrice).toFixed(5) }} {{ ticker }}</strong>
            </div>
          </div>
          <div class="coin-card__amount">
            <div class="coin-card__amount-label">
              Minimum Tickets Amount
            </div>

            <div class="coin-card__amount-value">
               <strong>{{ minTickets }}</strong>
            </div>
          </div>
        </div>
        <div class="coin-card__button" @click="showPlaceBetModal()">
          <span>Place Bet</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CoinCard',
  props: {
    name: String,
    ticker: String,
    ticketPrice: String,
    minTickets: String
  },
  methods: {
    showPlaceBetModal () {
      this.$modal.show('place-bet-modal', {
        buyTicker: this.ticker,
        buyCoinName: this.name,
        minBuyAmount: new Number(this.minTickets),
        buyPrice: this.ticketPrice,
        prizeTicker: 'BTC',
        prizeCoinName: 'Bitcoin'
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  /* For mobile: */
  .coin-card {
    position: relative;
    width: 100%;
    padding-top: 45%;
    opacity: 0.8;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.2);
    text-align: center;
  }

  .coin-card__wrapper {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
  }

  .coin-card__main {
    width: 60%;
    display: flex;
    flex-flow: column;
    justify-content: flex-end;
    background-color: #fff;
  }

  .coin-card__logo {
    margin-bottom: 5%;
  }

  .coin-card__logo img {
    width: 40%;
  }

  .coin-card__name {
    padding-bottom: 5%;
    font-size: 16px;
    font-weight: 700;
    color: #666;
  }

  .coin-card__details {
    width: 40%;
    display: flex;
    flex-flow: column;
    justify-content: flex-start;
  }

  .coin-card__market {
    height: 72%;
    display: flex;
    flex-flow: column;
    justify-content: center;
    background-color: #141414;
    padding: 2% 0%;
  }

  .coin-card__amount {
    margin-top: 5%;
  }

  .coin-card__price-label,
  .coin-card__amount-label {
    font-size: 13px;
    color: #ccc;
    margin-bottom: 5px;
  }

  .coin-card__price-value,
  .coin-card__amount-value {
    font-size: 16px;
    color: #fff;
  }

  .coin-card__button {
    height: 28%;
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    background-color: #ff9e00;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }

  .coin-card__button:hover {
    background-color: #ff8000;
  }

  .coin-card__button span {
    font-size: 16px;
    color: #fff;
  }

  /* For tablet: */
  @media only screen and (min-width: 768px) {
    .coin-card__amount {
      margin-top: 8%;
    }
  }

  /* For large desktop: */
  @media only screen and (min-width: 1440px) {
    .coin-card__amount {
      margin-top: 12%;
    }
  }
</style>
