<template>
  <div id="app">
    <Splash />
    <api-request
      :resource="browse()"
      v-model="geoblock"
      effect="fadeIn">
      <router-view v-if="geoblock.data.block === false" />
      <GeoBlock v-if="geoblock.data.block === true" />
    </api-request>
  </div>
</template>

<script>
import GeoBlock from '@/components/GeoBlock.vue'
import Splash from '@/components/Splash.vue'

export default {
  name: 'app',
  data () {
    return {
      geoblock: {
        data: {}
      }
    }
  },
  components: {
    GeoBlock,
    Splash
  },
  methods: {
      browse () {
          return (params) => {
              return this.$api.getGeoCheck(params)
          }
      }
  }
}
</script>

