<template>
  <BasePage>
    <div class="settings" @click="showAddressFollowingModal()">
      <font-awesome-icon icon="cog" size="1x" />
    </div>
    <tabs :options="{ useUrlFragment: false, defaultTabHash: 'tickets-tab' }">
        <tab id="tickets-tab" name="Tickets Bought">
            <TicketsTab/>
        </tab>
        <tab id="payments-tab" name="Unticketized Payments">
            <PaymentsTab/>
        </tab>
        <tab id="invoices-tab" name="Unpaid Invoices">
            <ChangellyTab/>
        </tab>
        <tab id="balance-tab" name="Available Balance">
            <LeftOverTab/>
        </tab>
    </tabs>
    <div style="height: 850px" />
  </BasePage>
</template>

<script>
import BasePage from '@/pages/BasePage.vue'
import TicketsTab from '@/components/TicketsTab.vue'
import PaymentsTab from '@/components/PaymentsTab.vue'
import LeftOverTab from '@/components/LeftOverTab.vue'
import ChangellyTab from '@/components/ChangellyTab.vue'

export default {
  name: 'MyBets',
  data: function () {
    return {
      betInfo: {},
    }
  },
  components: {
      BasePage,
      TicketsTab,
      PaymentsTab,
      LeftOverTab,
      ChangellyTab
  },
  methods: {
    getCoinLogo: (ticker) => {
      return require('../assets/coins/' + ticker + '.png')
    },
    load () {
        return (params) => this.$api.getMyBets(params)
    },
    showTicketsModal (addressId, drawId) {
      this.$modal.show('tickets-modal', {
        addressId: addressId,
        drawId: drawId
      });
    },
    showAddressFollowingModal () {
      this.$modal.show('address-following-modal');
    }
  }
}
</script>


<style>
  /* For mobile: */
  .settings {
    float: right;
    position: relative;
    top: 0px;
    cursor: pointer; right: 10px;
    opacity: 0.7;
  }

  .settings:hover {
    opacity: 1;
  }

  .tabs-component-tabs {
    display: flex;
    flex-flow: column;
    justify-content: flex-start;
    margin: 40px 30px 0px 0px;
    padding: 0;
    list-style: none;
  }

  .tabs-component-tab {
    font-size: 18px;
    font-weight: 700;
    cursor: pointer;
    margin-bottom: 20px;
  }

  .tabs-component-tab a {
    color: #2c3e5094;
  }

  .tabs-component-tab:hover .tabs-component-tab-a,
  .tabs-component-tab.is-active .tabs-component-tab-a {
    color: #2c3e50;
  }

  /* For tablet: */
  @media only screen and (min-width: 768px) {
    .tabs-component-tabs {
      flex-flow: row wrap;
      margin: 40px 30px 10px 0px;
    }

    .tabs-component-tab {
      margin-right: 20px;
    }
  }
</style>
